import React from 'react';
import { Menu, Icon, Input, Typography, Row, Col } from 'antd';

const { Title, Paragraph, Text } = Typography;
const { Search } = Input;

const AppTopbar = () => {
    return(
        <div>
          <Menu mode="horizontal">
              <Row>
            <Col span={1}>
                <Menu.Item key="mail">
                    <Icon type="home" style={{ fontSize: '30px'}} />
                </Menu.Item>
            </Col>
            <Menu.Item key="search">
                <Search
                    placeholder="input search text"
                    enterButton="Search"
                    size="midum"
                /> 
            </Menu.Item>
            <Menu.Item key="subject">
                <Typography className="">
                    <Title type="secondary" level={2} style={{color:'blue'}}>RemindFeedBack</Title>
                </Typography>
            </Menu.Item>
            <Menu.Item key="bell">
                <Icon type="bell" style={{ fontSize: '30px'}} />
            </Menu.Item>
            </Row>
          </Menu>   
        </div>
    );
};

export default AppTopbar;